playtotv.define('playtotv.user.User', [
	'playtotv.model.ObjectModel'
], function(
	ObjectModel
) {

	'use strict';

	var ptv = playtotv;

	return class User extends ObjectModel {
		/** @lends playtotv.user.User.prototype */

		/**
		 * User data model class.
		 *
		 * @extends playtotv.model.ObjectModel
		 * @constructs
		 * @param {playtotv.server.Server} server Server
		 * @param {Object} data User data to initialize with
		 * @param {string} data.id User id
		 * @param {string} [data.displayName] User name
		 * @param {string} [data.avatarUrl] User avatar URL
		 * @param {Object} [data.profileProperties={}] User profile property map
		 *
		 * @property {playtotv.server.Server} server Server
		 * @property {string} id User id
		 * @property {string} displayName User name
		 * @property {string} avatarUrl User avatar URL
		 * @property {playtotv.model.ObjectModel} profileProperties User profile property map
		 */
		constructor(server, data) {
			ptv.params(data, {
				id: ptv.REQUIRED,
				displayName: undefined,
				avatarUrl: undefined,
				profileProperties: {}
			});

			super({
				id: data.id,
				displayName: data.displayName,
				avatarUrl: data.avatarUrl,
				profileProperties: new ObjectModel(data.profileProperties)
			});

			this.server = server;
		}

		/** Dispose */
		dispose() {
			this.profileProperties.dispose();
			ObjectModel.prototype.dispose.call(this);
		}
	};
});
