playtotv.define('playtotv.mvc.BindWidget', [
	'playtotv.widget.Widget'
], function(
	Widget
) {

	'use strict';

	var ptv = playtotv;
	var event = ptv.event;

	return class BindWidget extends Widget {

		constructor(node, options) {
			super(node);

			var { app, path } = ptv.params(options, {
				app: ptv.REQUIRED,
				path: ptv.REQUIRED
			});

			this.app = app;
			this.path = path;

			// If app value is defined then initialize input to match
			// else initialize app to match input
			var value = app.seek(path);
			if (value !== undefined) {
				this.updateInput(value);
			} else {
				this.updateApp();
			}

			// Bind input to app
			// TODO: Only updateApp after input and/or form are validated?
			// See if/how this can tie in with our FormWidget widget
			var onInputChange = () => this.updateApp();
			this.addEventListener(node, 'change', onInputChange);
			this.addEventListener(node, 'keyup', onInputChange);

			// Bind app to input
			this.subscribe(app, path, value => this.updateInput(value));
		}

		updateApp() {
			var node = this.node;
			var value = node.type === 'checkbox' ? node.checked : node.value;
			this.app.set(this.path, value);
		}

		updateInput(value) {
			var node = this.node;
			if (node.type === 'checkbox') {
				if (value !== node.checked) {
					node.checked = value;
					event.triggerNative(node, 'change');
				}
			} else {
				if (value !== node.value) {
					node.value = value;
				}
			}
		}
	};
});
