playtotv.define('playtotv.mvc.UpdateWidget', [
	'playtotv.widget.Widget',
	'playtotv.cron'
], function(
	Widget,
	cron
) {

	'use strict';

	var ptv = playtotv;

	/**
	 * Run script starting at specified time and repeat with specified interval.
	 *
	 * Note: Script can also include a context by using JSON encoding (e.g: with {{script}} helper)
	 *
	 * @params {Object} options
	 * @params {playtotv.mvc.App} options.app
	 * @params {string} options.script
	 * @params {number} [options.start=Date.now()]
	 * @params {number} [options.repeat=10*60*1000]
	 */
	return class UpdateWidget extends Widget {

		constructor(node, options) {
			var { app, script, start, repeat } = ptv.params(options, {
				app: ptv.REQUIRED,
				script: ptv.REQUIRED,
				start: Date.now(),
				repeat: 10 * 60 * 1000
			});

			super(node);

			this.job = cron.at(start, () => {
				app.runScript(script);
			}, { repeat: repeat });
		}

		dispose() {
			cron.cancel(this.job);
			super.dispose();
		}
	};
});
