playtotv.define('playtotv.relation.RelationSubscription', [
	'playtotv.pubsub.Subscription'
], function(
	Subscription
) {

	'use strict';

	var util = playtotv.util;

	return class RelationSubscription extends Subscription {
		/** @lends playtotv.relation.RelationSubscription.prototype */

		/**
		 * Class used to represent a {@link playtotv.relation.RelationManager} Subscription.
		 *
		 * If selector is a string it is converted to a RegExp that enables matching multiple space separated rels.
		 * And this.regexIndex is set so that publishes are delivered to only the matching part of the channel string.
		 *
		 * @extends playtotv.pubsub.Subscription
		 * @constructs
		 * @param {string|RegExp} selector Relation selector string or RegExp
		 * @param {Function} handler Subscription callback
		 * @throws {TypeError} If selector or handler is invalid
		 *
		 * @property {RegExp} selector Relation selector RegExp
		 * @property {Function} handler Subscription callback
		 * @property {number} regexIndex Index of selector RegExp match result to deliver to
		 */
		constructor(selector, handler) {
			var isStr = typeof selector === 'string';
			if (isStr) {
				selector = new RegExp('(\\s|^)(' + util.escapeRegEx(selector).replace(/\\\*/g, '\\S*?') + ')(\\s|$)');
			}

			super(selector, handler);

			if (isStr) {
				this.regexIndex = 2;
			}
		}

		/**
		 * Deliver data to handler on specified channel.
		 *
		 * If regexIndex is defined then channel is replaced by specified index of the selector matches.
		 *
		 * @param {mixed} [data] Data to deliver
		 * @param {string} channel Channel we are delivering to
		 */
		deliver(data, channel) {
			if (this.handler) {
				var regexIndex = this.regexIndex;
				if (regexIndex !== undefined) {
					channel = this.selector.exec(channel)[regexIndex];
				}

				this.handler(data, channel);
			}
		}
	};
});
