/*jshint maxparams:100*/
playtotv.define('playtotv.mvc.App', [
	'playtotv.model.modelUtil',
	'playtotv.mvc.BindWidget',
	'playtotv.mvc.Controller',
	'playtotv.mvc.helpers',
	'playtotv.mvc.Router',
	'playtotv.mvc.UpdateWidget',
	'playtotv.mvc.View',
	'playtotv.storage.AsyncStorage',
	'playtotv.storage.MockAsyncStorage',
	'playtotv.touch.touchManager',
	'playtotv.PauseDog'
], function(
	modelUtil,
	BindWidget,
	Controller,
	helpers,
	Router,
	UpdateWidget,
	View,
	AsyncStorage,
	MockAsyncStorage,
	touchManager,
	PauseDog
) {

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;
	var event = ptv.event;
	var Cancellable = Promise.Cancellable;

	return class App extends Controller {
		/** @lends playtotv.mvc.App.prototype */

		/**
		 * PlayToTV MVC App containing global resources like templates, storage, router and view
		 * and coordinating controllers, widgets and event listeners.
		 *
		 * @example
		 * var app = new App({ id: 'foo' });
		 * app.addControllers([
		 *   [ 'bar', BarController ]
		 * ]).then(() => app.startView());
		 *
		 * @constructs
		 * @param {Object} options
		 * @param {string} options.id App id to use for logging and persistence
		 * @param {[Function]} [options.templates=Handlebars.templates] Page template functions to use
		 * @param {Object} [options.store={}] Properties with default values to be serialized and persisted
		 * @param {Object} [options.log={}] Log level map (false to disable)
		 * @param {boolean} [options.storage={}] Options for Storage (mock: true to use mock storage)
		 * @param {Object} [options.router={}] Options for Router
		 * @param {Object} [options.touch={}] Options for capturing touch (false to disable)
		 * @param {Object} [options.view={}] Options for View
		 * @param {number} [options.reloadTime=4*60*60*1000] JavaScript pause time in ms after which to reload
		 */
		constructor(options) {
			var Handlebars = window.Handlebars;
			if (Handlebars) {
				options.templates = options.templates || Handlebars.templates;
				Handlebars.registerHelper(helpers);
			}

			ptv.params(options, {
				id: ptv.REQUIRED,
				templates: ptv.REQUIRED,
				store: {},
				log: {},
				storage: {},
				router: {},
				touch: {},
				view: {},
				reloadTime: 4 * 60 * 60 * 1000
			});

			// Start without app ref or stored data (first call super and constructStorage)
			super({
				name: 'app',
				app: null,
				store: options.store
			});

			// TODO: JSDOC or privatize (_ prefix) properties
			this.app = this;
			this.id = options.id;
			this.templates = options.templates;
			this.logEnabled = this.constructLog(options.log);
			this.storage = this.constructStorage(options.storage);
			this.router = this.constructRouter(options.router);
			this.touchCaptured = this.constructTouch(options.touch);
			this.view = this.constructView(options.view);
			this.pauseDog = this.constructPauseDog(options.reloadTime);

			this.widgetMap = {};
			this.widgets = [];
			this.scriptListeners = [];

			event.addWindowOnError(this.onError.bind(this));
			event.addListener(window, 'beforeUnload', () => this.dispose());

			this.setWidgets([
				[ 'bind', BindWidget ],
				[ 'update', UpdateWidget ]
			]);

			// Deserialize app from storage and persist it
			this.ready = this.storage.get('app').catch(() => undefined).then(stored => {
				this.deserialize(stored);
				this.subscribe('*', ptv.debounce(() => this.flush('app'), 500));
			});
		}

		// TODO: JSDOC or privatize (_ prefix) methods

		////
		// CONSTRUCTORS
		//

		constructLog(options = {}) {
			if (!options) {
				return false;
			}

			this.logger = ptv.Logger.get(this.id);

			// Extend options with defaults, but keep passed option properties on top (e.g: higher priority)
			ptv.each({
				'error': 'ERROR',
				'view.slow': 'WARNING',
				'view.update': 'DEBUG',
				'view.diffs.*': 'SPAM',
				'*': 'DEBUG' // e.g: default level
			}, (value, key) => {
				if (!options.hasOwnProperty(key)) {
					options[key] = value;
				}
			});

			// Convert map to array with regex-es to facilitate use by this.log()
			this.logRules = Object.keys(options).map((key) => ({
				selector: key.indexOf('*') >= 0 ? util.globToRegEx(key) : key,
				level: options[key]
			}));

			this.subscribe('*', (data, channel) => this.log(channel, data));

			return true;
		}

		constructStorage(options) {
			var { mock } = options;
			var StorageClass = mock ? MockAsyncStorage : AsyncStorage;
			return new StorageClass(this.id + '.');
		}

		constructRouter(options) {
			var router = new Router(Object.assign({
				patterns: Object.keys(this.templates)
			}, options));

			router.subscribe('*', (params, pattern) => {
				// Note: this will also trigger view.update subscription below
				this.publish('route:' + pattern, params);
			});

			return router;
		}

		constructTouch(options) {
			if (!options) {
				return false;
			}

			touchManager.captureEvents(document, options);
			return true;
		}

		constructView(options) {
			var { throttle, interval, isSlow } = ptv.params(options, {
				throttle: 100,
				interval: 10000, // continuous update interval
				isSlow: 100
			});

			// View render template is determined by current route, the render context is this app
			// Note: In case a Handlebars SafeString is returned it is converted to plain string
			var view = new View({
				render: () => '' + this.templates[this.router.pattern](this),
				throttle: throttle,
				interval: interval,
				onNodeAdded: this.onNodeAdded.bind(this),
				onNodeRemove: this.onNodeRemove.bind(this),
				onAttrSet: this.onAttrSet.bind(this),
				onAttrRemove: this.onAttrRemove.bind(this)
			});

			view.subscribe('update', (update) => {
				var { timer: { total, string }, diffs } = update;
				this.log('view.' + (total > isSlow ? 'slow' : 'update'), string);
				if (diffs.length > 0) {
					this.log('view.diffs.' + diffs.length, diffs);
				}
			});

			return view;
		}

		constructPauseDog(reloadTime) {
			// Reload if JS was paused for more then x (e.g: 4 hours) to prevent stale clients (e.g: in native wrapper)
			// Also can help to prevent or work around other issues in these types of situations
			var pauseDog = new PauseDog();
			pauseDog.add(reloadTime, () => this.reload());
			return pauseDog;
		}

		////
		// STARTERS
		//

		/**
		 * Call this when you are ready to have things rendered to the view.
		 *
		 * E.g: After you have added your controllers.
		 */
		startView() {
			this.view.update();

			// Update view again whenever something is published
			this.subscribe('*', this.view.update);
		}

		////
		// CONTROLLERS
		//

		addController(name, ControllerClass, options) {
			return this.ready.then(() => {
				return this.storage.get(name).catch(() => undefined).then((stored) => {
					if (this[name]) {
						throw new Error('Controller name not available: ' + name);
					}

					var controller = new ControllerClass({
						name: name,
						app: this,
						stored: stored
					}, options);

					this[name] = controller;

					var debouncedFlush = ptv.debounce(() => this.flush(name), 500);

					// Pass on controller publishes to App and persist it's state
					controller.subscribe('*', (data, channel) => {
						this.publish(name + '.' + channel, data);
						debouncedFlush();
					});

					this.publish(name, controller);

					return controller;
				});
			});
		}

		addControllers(controllers) {
			return Promise.all(controllers.map(args => this.addController.apply(this, args)));
		}

		getController(name, timeout = 10000) {
			var controller = this[name];
			if (controller) {
				return Cancellable.resolve(controller);
			}

			var cancellable = this.when(name);

			if (timeout) {
				cancellable.setTimeout(timeout);
			}

			return cancellable;
		}

		////
		// CONTROLLER OVERRIDES
		//

		/**
		 * Log code and details using this.logger with level based on matching code with app.logRules.
		 *
		 * Overrides Controller.log() that delegates to this.
		 *
		 * @param {string} code
		 * @param {string|Object} [details=null]
		 */
		log(code, details = null) {
			if (!this.logEnabled) {
				return;
			}

			var rule = this.logRules.find((rule) => {
				var { selector } = rule;
				return typeof selector === 'string' ? selector === code : selector.test(code);
			});
			var level = rule && rule.level;
			if (!level) {
				return;
			}

			if (details && details.toObject) {
				details = modelUtil.toObject(details);
			}

			var args = typeof details === 'object' ? [ code, details ] : [ code + ': ' + details ];
			this.logger.log(level, args);

			// Return log level to facilitate extending functionality in classes extending this one
			return level;
		}

		/**
 		 * Persists specified controller's data to storage.
 		 *
		 * Overrides Controller.flush() that delegates to this.
 		 *
 		 * @returns {Promise} Promise of data being persisted
 		 */
		flush(name) {
			return this.storage.set(name, this[name].serialize());
		}

		// Override so set is called on Controller instance, thus also properly publishing on that
		set(path, value, options) {
			var head = util.getPathHead(path);
			var tail = util.getPathTail(path);
			if (tail) {
				this[head].set(tail, value, options);
			} else {
				super.set(path, value, options);
			}
		}

		////
		// WIDGETS
		//

		/**
		 * Registers widget by name with specified Widget class and base options.
		 *
		 * Overrides Controller.setWidget() that delegates to this.
		 *
		 * @param {string} name Widget name
		 * @param {[type]} WidgetClass Class to use for Widget instantiation
		 * @param {Object} [options={}] Base options
		 */
		setWidget(name, WidgetClass, options = {}) {
			options.app = this;

			this.widgetMap[name] = {
				WidgetClass: WidgetClass,
				options: options
			};
		}

		instantiateWidget(node, name, options) {
			var widget = this.widgetMap[name];
			if (!widget) {
				throw new Error('Widget not found: ' + name);
			}

			var instanceOptions = Object.assign({}, widget.options, options);
			var instance = new widget.WidgetClass(node, instanceOptions);

			this.widgets.push({ node: node, name: name, instance: instance });

			return instance;
		}

		disposeWidgets(by, value) {
			this.widgets = this.widgets.filter(widget => {
				if (!by || widget[by] === value) {
					widget.instance.dispose();
					return false;
				}
				return true;
			});
		}

		setNodeWidgets(node, widgets, oldWidgets) {
			// Cleanup any previously instantiated widgets (e.g: on attribute change)
			// TODO: Leave identical widgets in place instead of disposing and re-instantiating all of them
			if (oldWidgets) {
				this.disposeWidgets('node', node);
			}

			// If widgets looks like JSON then decode them (e.g: encoded by {{widgets}})
			// Otherwise this is parsed as a space separated array of widget name strings
			widgets = widgets.indexOf('[["') === 0 ? JSON.parse(widgets) :
				widgets.split(/\s+/).map(name => [ name, {} ]);

			widgets.forEach(widget => {
				var [ name, options ] = widget;
				this.instantiateWidget(node, name, options);
			});
		}

		////
		// SCRIPTS
		//

		runScript(script, context, ret) {
			var promise = new Promise(resolve => {
				// If script looks like JSON then decode, e.g: when encoded by {{on}} or {{script}}
				var scriptContext;
				if (script.indexOf('["') === 0) {
					[ script, scriptContext ] = JSON.parse(script);
				}

				// Combine a ref this app's properties, the script and the run context
				context = Object.assign({}, this, scriptContext, context);

				resolve(util.eval(script, context));
			});

			if (ret) {
				return promise;
			} else {
				promise.catch(error => this.onError(error));
			}
		}

		setNodeScript(node, type, script) {
			var { scriptListeners } = this;
			var listener = scriptListeners.find(listener => listener.node === node && listener.type === type);

			// Cleanup previous listener
			if (listener) {
				event.removeListener(listener);
				scriptListeners.splice(scriptListeners.indexOf(listener), 1);
			}

			// Note: node-added is handled in onNodeAdded and node-remove in onNodeRemove
			if (!script || type === 'node-added' || type === 'node-remove') {
				return;
			}

			listener = event.addListener(node, type, (event) => {
				this.runScript(script, { node: node, event: event });
			});
			scriptListeners.push(listener);
		}

		////
		// VIEW MUTATION OBSERVERS
		//

		onNodeAdded(node) {
			var script = node.getAttribute('on-node-added');
			if (script) {
				this.runScript(script, { node: node });
			}
		}

		onNodeRemove(node) {
			var script = node.getAttribute('on-node-remove');
			if (script) {
				this.runScript(script, { node: node });
			}
		}

		onAttrSet(node, name, value, oldValue) {
			if (name === 'widgets') {
				this.setNodeWidgets(node, value, oldValue);
			} else if (name.indexOf('on-') === 0) {
				this.setNodeScript(node, name.substr(3), value);
			}
		}

		onAttrRemove(node, name/*, value*/) {
			if (name === 'widgets') {
				this.disposeWidgets('node', node);
			} else if (name.indexOf('on-') === 0) {
				this.setNodeScript(node, name.substr(3), '');
			}
		}

		////
		// APP HELPERS
		//

		/**
		 * If target is truthy then this delegates to popup().
		 * Otherwise the window location is changed to specified URL.
		 *
		 * @param {string} url
		 * @param {string} [target]
		 * @param {Object} [options]
		 */
		open(url, target, options) {
			if (target) {
				return this.popup(url, target, options);
			}

			window.location = url;
		}

		/**
		 * Adds on-click="event.preventDefault(); app.open(node.href, '${target}')" to anchors of (3rd party) HTML.
		 * That in turn can go through popup() which can be overridden to use certain Native Wrapper commands etc.
		 *
		 * @param {string} html HTML to openify
		 * @param {Object} [options]
		 * @param {string} [options.target=''] Target to open openified anchor links in
		 * @returns {string} Openified version of html
		 */
		openify(html, options) {
			var { target } = ptv.params(options, { target: '' });
			return html && html.replace(/<a /g,
				`<a on-click="event.preventDefault(); app.open(node.href, '${target}')" `
			);
		}

		/**
		 * Opens url with window.open (through playtotv.util.popup) and returns Window reference.
		 *
		 * If you are using NativeController you will want to override this method and delegate to it's popup method.
		 *
		 * @param {string} url
		 * @param {string} [target]
		 * @param {Object} [options]
		 * @param {Object} [options.width=0]
		 * @param {Object} [options.height=0]
		 * @returns {Window}
		 */
		popup(url, target, options) {
			var { width, height } = ptv.params(options, { width: 0, height: 0 });
			return util.popup(url, target, { width: width, height: height });
		}

		/**
		 * Go back to previous url using window history.
		 */
		back() {
			// Note: Android does not seem to support window.history.back(), hence the history.go(-1)
			window.history.go(-1);
		}

		/**
		 * Reloads this app at it's current location.
		 */
		reload() {
			window.location.reload(true);
		}

		/**
		 * Clears storage and then reloads to make a clean app start.
		 */
		reset() {
			this.storage.clear().finally(() => this.reload());
		}

		/**
		 * Logs error and changes app error property if it's name and/or message differ from any current error.
		 *
		 * Your app should display this to the user via it's templates or by subscribing to the error publish.
		 *
		 * @params {Error} error
		 */
		onError(error) {
			// Ignore repeating errors; setting (and publishing and rendering) again could lead to infinite error loops
			var lastError = this.error;
			if (lastError && error.name === lastError.name && error.message === lastError.message) {
				return;
			}

			this.set('error', error, { json: false });
		}

		clearError() {
			if (this.error) {
				delete this.error;
				this.publish('error-cleared');
			}
		}

		////
		// DISPOSE
		//

		dispose() {
			this.disposeWidgets();

			this.pauseDog.dispose();

			this.view.dispose();

			if (this.touchCaptured) {
				touchManager.releaseEvents(document);
			}

			this.router.dispose();

			super.dispose();
		}
	};
});
