playtotv.define('playtotv.widget.VisualWidget', [
	'playtotv.widget.Widget'
], function(Widget) {

	'use strict';

	var ptv = playtotv;

	/**
	 * Scales a contained image like background-size: cover or contain; The container must have (fixed or scaling)
	 * dimensions in both width and height for the image to resize itself correctly. The container should have its
	 * overflow set to hidden for optimal effect, but this is not required per se. Positioning is done using top
	 * and/or left margins on the image.
	 *
	 * @class
	 * @name VisualWidget
	 * @memberOf playtotv.widget
	 * @extends playtotv.widget.Widget
	 * @param {Node} node
	 * @param {object} [options]
	 * @param {string} [options.mode='cover'] Sizing mode, either cover or contain
	 * @param {string} [options.image='img'] Image selector
	 */
	return class VisualWidget extends Widget {

		constructor(node, options) {
			super(node);

			options = ptv.params(options, {
				mode: 'cover',
				image: 'img'
			});

			this.image = $(options.image, node);
			this.mode = options.mode;
			this.handleResize = this.handleResize.bind(this);

			if (this.image) {
				this.addEventListener(window, 'resize', this.handleResize);
				this.addEventListener(this.image, 'load', this.handleResize);
				this.handleResize();
			}
		}

		/** @lends playtotv.widget.VisualWidget.prototype */

		/**
		 * Resize handler, automatically called onload of the image, and onresize of the window.
		 * @param {Event} event
		 */
		handleResize() {
			if (!this.node) {
				return; // widget already exited in the mean while
			}

			var node = this.node;
			var img = this.image;
			var mode = this.mode;
			var css = img.style;

			img.removeAttribute('width');
			img.removeAttribute('height');
			css.margin = 0;

			var width = node.offsetWidth;
			var height = node.offsetHeight;

			var imageRatio = img.offsetWidth / img.offsetHeight;
			var viewRatio = width / height;

			if ((viewRatio < imageRatio && mode === 'contain') ||
				(viewRatio > imageRatio && mode === 'cover')) {

				css.marginTop = -((width / imageRatio) - height) / 2 + 'px';
				img.setAttribute('width', '100%');

			} else {
				css.marginLeft = -((height * imageRatio) - width) / 2 + 'px';
				img.setAttribute('height', '100%');
			}
		}
	};
});
