playtotv.define('playtotv.Timer', [], function() {

	'use strict';

	/**
	 * A simple class for tracking timings and producing a summarizing string for those.
	 *
	 * Parts of the total timing interval can be marked using the start() and end() methods.
	 * Note: Those parts should not overlap, otherwise the unaccounted time will be incorrect.
	 */
	return class Timer {
		constructor() {
			this.t0 = Date.now();
			this.accounted = 0;
			this.timings = [];
		}

		start() {
			this.tx = Date.now();
		}

		end(label) {
			var time = Date.now() - this.tx;
			delete this.tx;
			this.accounted += time;
			this.timings.push(label + ': ' + time);
		}

		finish() {
			this.total = Date.now() - this.t0;
			this.unaccounted = this.total - this.accounted;

			var { timings, total, unaccounted } = this;
			var string = total + 'ms';
			if (timings.length > 0) {
				string += ' (' + timings.join(', ') + ')';
			}
			if (unaccounted) {
				string += ' +' + unaccounted;
			}

			this.string = string;

			return string;
		}
	};
});
