playtotv.define('playtotv.widget.TransformableWidget', [
	'playtotv.animationFrame',
	'playtotv.css.Matrix',
	'playtotv.css.style',
	'playtotv.css.Vector',
	'playtotv.widget.Widget'
], function(
	animationFrame,
	Matrix,
	style,
	Vector,
	Widget
) {

	'use strict';

	var ptv = playtotv;
	var log = new ptv.Logger(this.id);

	var STATE_PASSIVE = 1;
	var STATE_ACTIVE = 2;

	return class TransformableWidget extends Widget {

		constructor(node, options) {
			super(node);

			options = ptv.params(options, {
				translate: true,
				scale: true,
				rotate: true
			});

			this.touches = [];
			this.transform = new Matrix();

			this.translation = new Vector(0, 0);
			this.scaling = 1;
			this.rotation = 0;

			this.deltaMove = new Vector(0, 0);
			this.deltaAngle = 0;
			this.deltaScale = 1;

			ptv.bindMethods(this, [
				'handleFrame',
				'touchStart',
				'touchMove',
				'touchEnd'
			]);

			this.addEventListener(node, 'em:touchstart', this.touchStart);
			this.addEventListener(node, 'em:touchmove', this.touchMove);
			this.addEventListener(node, 'em:touchend', this.touchEnd);
		}

		setState(state) {
			this.state = state;
		}

		getState() {
			return this.state;
		}

		requestFrame() {
			this.request = animationFrame.request(this.handleFrame);
		}

		cancelFrame() {
			if (this.request) {
				animationFrame.cancel(this.request);
				delete this.request;
			}
		}

		touchStart(e) {
			e.preventDefault();
			this.touches.push(e.detail.sequence.id);
		}

		touchMove(e) {
			e.preventDefault();

			var proxy = e.detail.proxy;
			var touches = this.touches;
			if (touches.length > 0) {

				var begin = proxy.getSequence(touches[0]);
				var translate = Vector.ZERO; // begin.offset; // TODO: unbug single-finger dragging
				var angle = 0;
				var scale = 1;

				if (touches.length > 1) {
					var end = proxy.getSequence(touches[1]);
					var origin = end.start.subtract(begin.start);
					var current = end.point.subtract(begin.point);

					angle = Math.atan2(-current.y, current.x) - Math.atan2(-origin.y, origin.x);
					scale = current.magnitude() / origin.magnitude();
					translate = begin.offset.add(end.offset).divideBy(2);
				}

				this.deltaAngle = angle;
				this.deltaScale = scale;
				this.deltaMove = translate;

				// start animating when actual dragging starts
				if (this.getState() !== STATE_ACTIVE) {
					this.setState(STATE_ACTIVE);
					this.requestFrame();
				}
			}
		}

		touchEnd(e) {
			log.d(e);
			e.preventDefault();

			var touches = this.touches;
			var removed = e.detail.sequence.id;
			var index = touches.indexOf(removed);
			if (index > -1) {
				touches.splice(index, 1);
			}

			this.translation = this.getTranslation();
			this.rotation = this.getRotation();
			this.scaling = this.getScaling();

			this.cancelFrame();
			this.setState(STATE_PASSIVE);
		}

		getTranslation() {
			return this.translation.add(this.deltaMove);
		}

		getRotation() {
			return this.rotation + this.deltaAngle;
		}

		getScaling() {
			return this.scaling * this.deltaScale;
		}

		handleFrame() {
			var translate = this.getTranslation();
			var rotation = this.getRotation();
			var scale = this.getScaling();

			var transform = this.transform.
				rotateZ(rotation).
				scale(scale).
				translate(translate);

			style.setTransform(this.node, transform);

			this.requestFrame();
		}
	};
});
