playtotv.define('playtotv.MutationObserver', [], function() {

	'use strict';

	// Abort if we are not running in a browser (e.g: in Node)
	if (typeof window === 'undefined') {
		return;
	}

	var event = playtotv.event;

	/**
	 * MutationRecord
	 * https://developer.mozilla.org/en-US/docs/Web/API/MutationObserver#MutationRecord
	 *
	 * @name MutationRecord
	 * @namespace
	 * @memberOf playtotv
	 * @property {String} type The event's type: childList, attributes or characterData
	 * @property {Node} target The affected node, for childList the inserted/removed node's parent, otherwise self.
	 * @property {Array} addedNodes Array of insterted nodes for childList mutations.
	 * @property {Array} removedNodes Array of removed nodes for childList mutations.
	 * @property {Node} previousSibling Previous sibling of inserted/removed nodes or null
	 * @property {Node} nextSibling Next sibling of inserted/removed nodes or null
	 * @property {String} attributeName The name of the attribute that changed or null
	 * @property {String} attributeNamespace The namespace of the changed attribute or null
	 * @property {String} oldValue The previous value of changed attributes or characterData, or null
	 */
	var mutationRecord = {
		type: null,
		target: null,
		addedNodes: [], // Spec says null, but actually returns an empty nodelist.
		removedNodes: [], // Spec says null, but actually returns an empty nodelist.
		previousSibling: null,
		nextSibling: null,
		attributeName: null,
		attributeNamespace: null,
		oldValue: null
	};

	// https://developer.mozilla.org/en-US/docs/Web/API/MutationObserver#MutationObserverInit
	var mutationOptions = {
		childList: false,
		attributes: false,
		characterData: false,
		subtree: false,
		attributeOldValue: false,
		characterDataOldValue: false,
		attributeFilter: false
	};

	/**
	 * DOM3 event to DOM4 mutationRecord processors
	 *
	 */
	var processors = {
		DOMNodeInserted: function(e, options, node) {
			var inserted = e.target;
			return (options.subtree || (inserted.parentNode === node)) ? playtotv.params({
				type: 'childList',
				target: inserted.parentNode,
				addedNodes: [ inserted ],
				previousSibling: inserted.previousSibling,
				nextSibling: inserted.nextSibling
			}, mutationRecord) : null;
		},

		DOMNodeRemoved: function(e, options, node) {
			var removed = e.target;
			return (options.subtree || (removed.parentNode === node)) ? playtotv.params({
				type: 'childList',
				target: removed.parentNode,
				removedNodes: [ removed ],
				previousSibling: removed.previousSibling,
				nextSibling: removed.nextSibling
			}, mutationRecord) : null;
		},

		DOMAttrModified: function(e, options, node) {
			var attr = e.attrName;
			var filter = options.attributeFilter;
			return (options.subtree || (e.target === node)) && (!filter || filter.indexOf(attr) >= 0) ?
				playtotv.params({
					type: 'attributes',
					target: e.target,
					attributeName: attr,
					oldValue: options.attributeOldValue ? e.prevValue : null
				}, mutationRecord) :
				null;
		},

		DOMCharacterDataModified: function(e, options, node) {
			return (options.subtree || (e.target === node)) ? playtotv.params({
				type: 'characterData',
				target: e.target,
				oldValue: options.characterDataOldValue ? e.prevValue : null
			}, mutationRecord) : null;
		}
	};

	/**
	 * MutationProxy,
	 *
	 */
	class MutationProxy {
		constructor(node, proxyEvent, observer) {
			this.node = node;
			this.proxyEvent = proxyEvent;
			this.observer = observer;
			this.processor = processors[proxyEvent];
			this.onModified = this.onModified.bind(this);
			event.addListener(node, proxyEvent, this.onModified);
		}

		onModified(e) {
			var observer = this.observer;
			var record = this.processor(e, observer.options, this.node);
			if (record) {
				observer.addRecord(record);
			}
		}

		disconnect() {
			event.removeListener(this.node, this.proxyEvent, this.onModified);
			delete this.node;
			delete this.proxyEvent;
			delete this.observer;
			delete this.processor;
		}
	}

	/**
	 * Mutation Observer pollyfill. The pollyfill is only applied if a native implementation is not supported.
	 * https://developer.mozilla.org/en-US/docs/Web/API/MutationObserver
	 *
	 * @name MutationObserver
	 * @memberOf playtotv
	 * @class
	 * @param {Function} callback
	 */
	class MutationObserver {

		constructor(callback) {
			this.callback = callback;
			this.listeners = [];
			this.records = [];
		}

		/** @lends playtotv.MutationObserver.prototype */

		/**
		 * Observes changes within a target Node and notifies them to the instance's callback as an array of
		 * mutation records, https://developer.mozilla.org/en-US/docs/Web/API/MutationObserver#MutationRecord.
		 * At least one type of mutations (childList, attributes and/or characterData) must be observed,
		 * or an error is thrown.
		 *
		 * @param {Node} target
		 * @param {MutationOptions} options
		 * @param {boolean} options.childList=false Observe changes to the target's child nodes when true
		 * @param {boolean} options.attributes=false Observe changes to the target's attributes when true
		 * @param {boolean} options.characterData=false Observe changes to the target's data when true
		 * @param {boolean} [options.subtree=false] Observe all descendent nodes' changes when true
		 * @param {boolean} [options.attributeOldValue=false] Record the previous attribute value when true
		 * @param {boolean} [options.characterDataOldValue=false] Record the previous data value when true
		 * @param {Array} [options.attributeFilter] Attribute names that should be observed rather than all of them
		 */
		observe(target, options) {
			// fill in the blanks
			options = this.options = playtotv.params(options, mutationOptions);

			// as per spec; options MUST define at least one type of DOM changes to monitor
			if (!options.childList && !options.attributes && !options.characterData) {
				throw new Error('An invalid or illegal string was specified');
			}

			var listeners = this.listeners;
			if (options.childList) {
				listeners.push(
					new MutationProxy(target, 'DOMNodeInserted', this),
					new MutationProxy(target, 'DOMNodeRemoved', this)
				);
			}

			if (options.attributes) {
				listeners.push(
					new MutationProxy(target, 'DOMAttrModified', this)
				);
			}

			if (options.characterData) {
				listeners.push(
					new MutationProxy(target, 'DOMCharacterDataModified', this)
				);
			}
		}

		/**
		 * Adds a record via a proxy, and notifies
		 * @private
		 */
		addRecord(record) {
			this.records.push(record);
			this.notify();
		}

		/**
		 * Empties the mutation record queue and returns any mutations it contained.
		 * The instance's callback will not be called for these mutations.
		 *
		 * @returns {Array} mutationRecords
		 */
		takeRecords() {
			var records = this.records;
			this.records = [];
			return records;
		}

		/**
		 * Takes the current record queue, and sends them to the instance's callback.
		 * @private
		 */
		notify() {
			var records = this.takeRecords();
			if (records.length) {
				this.callback(records);
			}
		}

		/**
		 * Disconnects all observers from this instance.
		 *
		 */
		disconnect() {
			this.listeners.forEach(function(listener) {
				listener.disconnect();
			});

			this.listeners = [];
		}
	}

	return window.MutationObserver || window.WebkitMutationObserver || window.MozMutationObserver || MutationObserver;
});
